@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

#mms {
  margin-top: 60px;
  margin-bottom: 60px;
}

@media (max-width: 575.98px) {
  #mms {
    margin-bottom: 30px;
    margin-top: 35px;
  }
}

#mms-container {
  display: flex;
  width: 100%;
}

@media (max-width: 767.98px) {
  #mms-container {
    flex-direction: column;
  }
}

#mms-container #mms-main-locations {
  background-color: #efefef;
  flex-grow: 1;
}

#mms-container #mms-main-locations #map {
  width: 100%;
  height: 100%;
}

@media (max-width: 767.98px) {
  #mms-container #mms-main-locations {
    width: 100%;
    height: 450px;
    margin-top: 20px;
  }
}

#mms-container #mms-sidebar-locations {
  flex-basis: 100%;
  background-color: white;
  overflow-y: auto;
}

@media (min-width: 768px) {
  #mms-container #mms-sidebar-locations {
    flex-basis: 480px;
  }
}

#mms-container #mms-sidebar-locations h1 {
  font-size: 28px;
}

#mms-container #mms-sidebar-locations h2 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}

#mms-container #mms-sidebar-locations .map-filter {
  margin: 10px 0;
  border: 2px solid #E8EDEF;
  padding: 10px 20px;
}

#mms-container #mms-sidebar-locations #no-results {
  margin: 20px;
}

#mms-container #mms-sidebar-locations #select-lifestyle {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: url("../../img/select-arrow.png");
  background-size: 13px auto;
  background-position-x: calc(100% - 15px);
  background-position-y: center;
  background-repeat: no-repeat;
  width: 100%;
  text-indent: 0.01px;
  color: #000;
  padding: 7px 15px;
  padding-right: 30px;
  margin: 10px 0;
  margin-bottom: 20px;
  font-size: 16px;
  border: 1px solid #999;
  border-radius: 0;
  box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}

#mms-container #mms-sidebar-locations select:focus,
#mms-container #mms-sidebar-locations button:focus {
  outline: 0;
}

#mms-container #mms-sidebar-locations input {
  width: 100%;
}

#mms-container #mms-sidebar-locations .map-title {
  padding: 0 20px;
}

#mms-container #mms-sidebar-locations #mms-filters {
  margin: 0px 20px;
}

@media (min-width: 768px) {
  #mms-container #mms-sidebar-locations #mms-filters {
    margin: 0px 47px 15px 0;
  }
}

#mms-container #mms-sidebar-locations #mms-filters select[name="state"] {
  width: 100%;
  padding: 10px;
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 10px 0 5px 0;
  font-size: 18px;
}

#mms-container #mms-sidebar-locations #mms-list {
  list-style-type: none;
  margin-left: 0px;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
  padding: 0px;
  overflow-y: scroll;
  max-height: 450px;
}

@media (min-width: 768px) {
  #mms-container #mms-sidebar-locations #mms-list {
    max-height: 800px;
    padding-right: 30px;
  }
}

#mms-container #mms-sidebar-locations #mms-list li {
  border-bottom: 1px solid #084C61;
  padding: 18px;
  margin-bottom: 0;
}

@media (max-width: 767.98px) {
  #mms-container #mms-sidebar-locations #mms-list li {
    padding: 20px;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .mms-listing-clickable {
  cursor: pointer;
}

#mms-container #mms-sidebar-locations #mms-list li .location-brand {
  color: #CC0000;
}

#mms-container #mms-sidebar-locations #mms-list li .location-img {
  display: inline-block;
  width: 60px;
  vertical-align: top;
}

#mms-container #mms-sidebar-locations #mms-list li .location-img img {
  width: 40px;
  float: left;
}

#mms-container #mms-sidebar-locations #mms-list li .location-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  font-family: "Gotham", "Arial", "Helvetica", sans-serif;
}

#mms-container #mms-sidebar-locations #mms-list li .location-title .title-mi {
  text-align: right;
}

#mms-container #mms-sidebar-locations #mms-list li .blue-pills {
  margin: 10px 0;
}

#mms-container #mms-sidebar-locations #mms-list li .blue-pills strong {
  font-weight: 500;
}

@media (max-width: 575.98px) {
  #mms-container #mms-sidebar-locations #mms-list li .blue-pills > div {
    color: #177E89;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .blue-pills ul {
  padding-left: 30px;
}

@media (max-width: 575.98px) {
  #mms-container #mms-sidebar-locations #mms-list li .blue-pills ul {
    padding-left: 35px;
  }
  #mms-container #mms-sidebar-locations #mms-list li .blue-pills ul li {
    font-weight: normal;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .blue-pills ul li {
  margin-top: 7px;
  padding: 0;
  border-bottom: none;
  list-style: initial;
}

#mms-container #mms-sidebar-locations #mms-list li .info-btns {
  margin-top: 20px;
}

@media (max-width: 767.98px) {
  #mms-container #mms-sidebar-locations #mms-list li .info-btns {
    text-align: center;
  }
  #mms-container #mms-sidebar-locations #mms-list li .info-btns a {
    width: 100%;
    max-width: 300px;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .info-btns a {
  margin: 2px;
  display: inline-block;
  width: auto;
  min-width: inherit;
  line-height: 19px !important;
  padding: 13px 25px !important;
}

#mms-container #mms-sidebar-locations #mms-list li .info-btns .mms-btn-phone {
  display: none;
}

#mms-container #mms-sidebar-locations #mms-list li .location-content {
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  font-size: 1.0625rem;
  line-height: 1.625rem;
}

@media (max-width: 575.98px) {
  #mms-container #mms-sidebar-locations #mms-list li .location-content {
    flex-direction: column;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.5rem;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .location-content .img-container {
  flex-basis: 150px;
  display: flex;
  overflow: hidden;
  justify-content: center;
}

#mms-container #mms-sidebar-locations #mms-list li .location-content .img-container img {
  height: 150px;
  max-width: none;
  width: auto;
}

@media (max-width: 575.98px) {
  #mms-container #mms-sidebar-locations #mms-list li .location-content .img-container {
    width: 100%;
    margin-bottom: 20px;
    flex-basis: 100%;
  }
  #mms-container #mms-sidebar-locations #mms-list li .location-content .img-container a {
    display: block;
    width: 100%;
  }
  #mms-container #mms-sidebar-locations #mms-list li .location-content .img-container img {
    width: 100%;
    height: auto;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .location-content .info-container {
  padding: 0 0 0 20px;
}

@media (max-width: 575.98px) {
  #mms-container #mms-sidebar-locations #mms-list li .location-content .info-container {
    padding: 0;
  }
}

#mms-container #mms-sidebar-locations #mms-list li .location-content .info-container .location-title {
  color: black;
  font-weight: bold;
  text-transform: uppercase;
}

#mms-container #mms-sidebar-locations #mms-list li .mms-distance {
  float: right;
}

#mms-container #mms-sidebar-locations #mms-list li .mms-btn {
  font-size: 13px !important;
  padding: 8px 14px;
  margin-right: 4px;
  border-radius: 6px;
  text-decoration: none;
  display: inline-block;
}

@media (min-width: 768px) and (max-width: 1199px) {
  #mms-container #mms-sidebar-locations #mms-list li .mms-btn {
    display: block;
  }
  #mms-container #mms-sidebar-locations #mms-list li .mms-btn.btn-location {
    margin-bottom: 7px;
  }
}

@media (max-width: 767px) {
  #mms-container #mms-sidebar-locations #mms-list li .mms-btn {
    font-size: 11px !important;
  }
}

#mms-container #mms-sidebar-locations .selected-list {
  background-color: #E9E7EE;
}

#mms-container #mms-sidebar-locations .selected-list .mms-btn {
  background-color: white;
}

.blue-pill {
  line-height: 1.625rem;
  background-color: #0B436A;
  display: inline-block;
  text-align: center;
  border-radius: 40px;
  color: white;
  margin-right: 10px;
  font-size: 10px;
  font-weight: bold;
  width: 26px;
  height: 26px;
}

#pills-legend {
  font-size: .875rem;
  list-style: none;
  text-indent: none;
  padding: 0;
}
